<template>
   <div :class="{ 'talkbg': true, 'talkbgcur': heg==1 }">
    <div class="bg">
      <el-scrollbar style="height: 100%;" ref="myScrollbar"> 
        <p class="more" v-if="total/5>pageNum" @click="moredata">点击查看更多</p>
        <template v-for="(item, index) in workMessageInfoDTOList">
           <div class="talk-cont" v-if="item.canDelete==0">
              <div class="talk">
                <div class="fl pic">
                  <img :src='item.senderType!=1?tea:stu' alt=""/>
                </div>
                <p class="fl">{{item.senderName}}   {{item.createtime}}</p>
              </div>
              <p class="text">{{item.content}}</p>
           </div>
           <div class="talk-cont talk-cont-self" v-else>
              <div class="talk">
                <div class="fl pic">
                  <img :src='item.senderType!=1?tea:stu' alt=""/>
                </div>
                <i class="el-icon-delete fl" @click="dele(item.id,index)"></i>
                <p class="fl">{{item.senderName}}   {{item.createtime}}</p>
              </div>
              <p class="text">{{item.content}}</p>
           </div>
        </template>
       </el-scrollbar>
     </div>
     <div class="push-bg">
       <el-input
          class="push-talk"
          type="textarea"
          placeholder="请输入内容"
          size="small" 
          v-model="talktext"
          rows="4" 
          maxlength="300"
          show-word-limit
        ></el-input>
        <p class="push-button"><el-button size="mini" @click="pus" type="primary">发表</el-button></p>
     </div>
   </div>
</template>

<script>
export default {
    name: 'talk',
    props:{
      workMessageInfoDTOList:{
        default:[]
      },
      total:{

      },
      heg:{
        default:0
      }
    },
    data() {
      return {
        size:10,
        talktext:'',
        pageNum:1,
        stu:require("../../assets/img/student.jpg"),
        tea:require("../../assets/img/teacher.jpg")
      };
    },
    updated: function() {
        // this.scrollDown()
    },
    watch:{
      workMessageInfoDTOList: {
        handler(newVal, oldVal) {
            if (newVal<=5) {
              this.pageNum=1;
            };
        },
        deep: true
      }
    },
    mounted(){
      if(this.pageNum==1){
        this.size=5
      }else{
        this.size=10
      }
    },
    methods: {
      scrollDown() {
          this.$refs['myScrollbar'].wrap.scrollTop = this.$refs['myScrollbar'].wrap.scrollHeight;
      },
      moredata(){
        this.pageNum = this.pageNum+1;
        console.log(this.pageNum)
        this.$emit('getmore',this.pageNum)
      },
      pus(){
        this.$emit('pus',this.talktext)
        this.talktext = ''
      },
      dele(id,index){
        var data = {
          id:id,
          index:index
        }
        this.$emit('dele',data)
      }
    }
};
</script>
<style type="text/css">
  .el-scrollbar__wrap{
  overflow-x: hidden;
}
</style>
<style lang='scss' scoped>
  .talkbg{
    height:550px;
    width:100%;
    padding-bottom:145px;
    box-sizing:border-box;
    overflow:hidden;
    border:1px solid #ccc;
    .bg{
      height:100%;
      padding:10px 20px;
      background:#f8f8f8;
      box-sizing:border-box;
      .more{
        text-align:center;
        line-height:16px;
        font-size:12px;
        color:#999;
        cursor:pointer;
      }
    }
    .talk-cont{
      width:70%;
      .talk{
        overflow:hidden;
        line-height:24px;
      }
      div.pic{
        width:24px;
        height:24px;
        background:#ccc;
        border-radius:24px;
        margin-right:6px;
      }
      div.pic img{
        display:block;
        width:24px;
        height:24px;
        border-radius:24px;
      }
      p.fl{
        color:#666;
        font-size:12px;
      }
      .text{
        font-size:12px;
        background:#fff;
        border:1px solid #eee;
        border-radius:5px;
        width:auto;
        max-width:100%;
        display:inline-block;
        padding:5px;
        margin-left:30px;
        line-height:18px;
        box-sizing:border-box;
      }
    }
    .talk-cont-self{
      margin-left:30%;
      overflow:hidden;
      box-sizing:border-box;
      .talk{
        float:right;
        .el-icon-delete{
          margin-right:10px;
          color:#f56c6c;
          cursor:pointer;
          margin-top:3px;
        }
        div.pic{
          float:right;
          margin-left:10px;
        }
      }
      .text{
        clear:both;
        width:auto;
        max-width:100%;
        float:right;
        background:#a1f58c;
        margin-right:30px;
      }
    }
  }
  .push-bg{
    height:120px;
    background:#fff;
    padding:10px 20px;
    box-sizing:border-box;
    .push-talk{
      border:0;
    }
    .push-button{
      float:right;
      margin-top:5px;
    }
  }
  .talkbgcur{
    height:100%;
  }
</style>


